.section__details {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2.5rem;
}

.card {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    border-radius: 6px;
    padding: 10px;
    background-color: var(--section-overlay);
}

.detail__card-icon {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 5px;
    margin-bottom: 15px;
}

.detail__card-icon>small {
    text-transform: capitalize;
}

@media screen and (max-width: 1024px) {
    .section__details {
        grid-template-columns: 1fr, 1fr;
    }
}