:root {
  --section-overlay: rgb(0, 0, 0, 0.5);
  --background-overlay: rgb(0, 0, 0, 0.2);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.App {
  width: 100%;
  height: 100vh;
  background-position: center;
  background-size: cover;
}

.overlay {
  width: 100%;
  height: 100vh;
  background-color: var(--background-overlay);
}

.container {
  max-width: 800px;
  margin: auto;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  padding: 1rem;
}

/* Top */

.section {
  width: 100%;
  padding: 1rem;
  border-radius: 0.4rem;
  color:whitesmoke;
}

.section__inputs, .section__temperature {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: var(--section-overlay);
}

.section__inputs>input {
  border: 1px solid whitesmoke;
  border-radius: 0.7rem;
  background-color: transparent;
  color:ghostwhite ;
  padding: 0.5rem;
  font-size: 20px;
  font-weight: 300;
}

.section__inputs>input:focus {
  outline: none;
}

.section__inputs>button {
  padding: 10px 50px;
  border: none;
  border-radius: .5rem;
  font-size: 20px;
  font-weight: 500;
  background-color: whitesmoke;
}

.section__inputs>button:hover {
  cursor: pointer;
  background-color: gainsboro;
}

.icon {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.icon>h3 {
  font-size: 18px;
  font-weight: 300;
  text-transform: capitalize;
}

.temperature>h1 {
  font-size: 65px;
}
